//other import
import React from 'react';
import styles_1 from './../../../scss/commonComponents/Card_1.module.scss';
import styles_2 from './../../../scss/commonComponents/Card_2.module.scss';
import fullStar from './../../../assets/img/fullStar.svg';
import compare from './../../../assets/img/compare.svg';
import basket from './../../../assets/img/basket.svg';
//components
import Button from "../Button/Button";

class Card extends React.Component {

    state = {
        wantToBuy: this.props.wantToBuy,
        currentTab: 1,
    };

    // smallFunc
    addToBuyNow_PROPS = () => {
        localStorage.setItem('buyNow', JSON.stringify([{...this.props}]));
    };

    //bigFunc
    minus = () => {
        if (!(this.state.wantToBuy <= 1)) {
            this.setState({wantToBuy: this.state.wantToBuy - 1});
            localStorage.setItem('buyNow', JSON.stringify([{
                ...this.props,
                wantToBuy: JSON.parse(localStorage.getItem('buyNow'))[0].wantToBuy - 1
            }]));
        } else {
            alert('Неможлива операція');
        }
    };

    plus = () => {
        if (!(this.state.wantToBuy >= this.props.amount)) {
            this.setState({wantToBuy: this.state.wantToBuy + 1});
            localStorage.setItem('buyNow', JSON.stringify([{
                ...this.props,
                wantToBuy: JSON.parse(localStorage.getItem('buyNow'))[0].wantToBuy + 1
            }]));
        } else {
            alert('хуйлан забагато хочеш');
        }
    };

    getProducts = () => {
        let products = localStorage.getItem('basket');

        if (products !== null) {
            return JSON.parse(products);
        } else {
            return [];
        }
    };

    addToBasket = (id) => {
        let products = this.getProducts();
        let identifiers = products.map(unit => unit.id);
        let index = identifiers.indexOf(id);
        if (index === -1) {
            products.push({...this.props});
        } else {
            products.splice(index, 1);
        }
        localStorage.setItem('basket', JSON.stringify(products));
    };

    // outFromBasket = () => {
    //
    // };

    render() {
        return (
            <div className={this.props.cardStyle2 ? styles_2.cardWrapper : styles_1.cardWrapper}>
                <div className={this.props.cardStyle2 ? styles_2.card : styles_1.card}>
                    <div style={{backgroundImage: `url(${this.props.img})`}}
                         className={this.props.cardStyle2 ? styles_2.img : styles_1.img}>
                        {this.props.status === 'new'
                            ? <div className={this.props.cardStyle2 ? styles_2.new : styles_1.new}>Новинка</div>
                            : this.props.status === 'action' ?
                                <div className={this.props.cardStyle2 ? styles_2.action : styles_1.action}>
                                    Акція</div> : null}
                    </div>

                    <div className={this.props.cardStyle2 ? styles_2.cardContent : styles_1.cardContent}>
                        <div
                            className={this.props.cardStyle2 ? styles_2.name : styles_1.name}>{this.props.name}</div>
                        <div className={this.props.cardStyle2 ? styles_2.starsAndOther : styles_1.starsAndOther}>
                            <img src={fullStar} alt=""/>
                            <img src={fullStar} alt=""/>
                            <img src={fullStar} alt=""/>
                            <img src={fullStar} alt=""/>
                            <img src={fullStar} alt=""/>

                            <img className={this.props.cardStyle2 ? styles_2.compare : styles_1.compare} src={compare}
                                 alt=""/>
                            <img onClick={() => this.addToBasket(this.props.id)} title='Добавити в корзину'
                                 className={this.props.cardStyle2 ? styles_2.basket : styles_1.basket} src={basket}
                                 alt=""/>
                        </div>
                        <div
                            className={this.props.cardStyle2 ? `${styles_2.comment} ${styles_2.common}` : `${styles_1.comment} ${styles_1.common}`}>
                            Залишити відгук
                        </div>
                        {this.props.amount !== 0 ?
                            <div
                                className={this.props.cardStyle2 ? `${styles_2.have} ${styles_2.common}` : `${styles_1.have} ${styles_1.common}`}>
                                В наявності: {this.props.amount}шт.</div> :
                            <div
                                className={this.props.cardStyle2 ? `${styles_2.havent} ${styles_2.common}` : `${styles_1.havent} ${styles_1.common}`}>
                                Немає</div>}
                        <div
                            className={this.props.cardStyle2 ? styles_2.producer : styles_1.producer}>{this.props.producer}</div>
                        <div
                            className={this.props.cardStyle2 ? `${styles_2.id} ${styles_2.common}` : `${styles_1.id} ${styles_1.common}`}>
                            Код товару: {this.props.id}</div>
                        <div className={this.props.cardStyle2 ? styles_2.buy : styles_1.buy}>
                            <div className={this.props.cardStyle2 ? styles_2.wantToBuy : styles_1.wantToBuy}>

                                {/*//wantToBuyNumber*/}
                                <span onClick={() => this.minus()}> </span>
                                <span>{this.state.wantToBuy}</span>
                                <span onClick={() => this.plus()}> </span>


                            </div>
                            <div
                                className={this.props.cardStyle2 ? styles_2.price : styles_1.price}>{this.props.price}<span>грн</span>
                            </div>
                            {/*//Купити*/}
                            <div className={this.props.cardStyle2 ? styles_2.buyButton1 : styles_1.buyButton1}
                                 onClick={() => this.addToBuyNow_PROPS()}>
                                <Button to='/purchaseOne' text='Купити'/>
                            </div>
                            <div className={this.props.cardStyle2 ? styles_2.buyButton2 : styles_1.buyButton2}>
                                <Button to='/purchaseOne' text='Купити'/>
                            </div>
                            {/*//Детальніше*/}
                            <div
                                className={this.props.cardStyle2 ? styles_2.moreDetailButton : styles_1.moreDetailButton}
                                onClick={() => this.addToBuyNow_PROPS()}>
                                <Button to='/moreDetail' text='Детальніше'/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={this.props.cardStyle2 ? styles_2.tabsWrapper : styles_1.tabsWrapper}>
                    <div className={styles_2.tabs}>
                        <div onClick={() => this.setState({currentTab: 1})}
                             className={this.state.currentTab === 1 ? styles_2.active : null}>Опис
                        </div>
                        <div onClick={() => this.setState({currentTab: 2})}
                             className={this.state.currentTab === 2 ? styles_2.active : null}>Характеристики
                        </div>
                        <div onClick={() => this.setState({currentTab: 3})}
                             className={this.state.currentTab === 3 ? styles_2.active : null}>Відгуки
                        </div>
                    </div>
                    <div className={styles_2.content}>
                        {
                            this.state.currentTab === 1
                                ? <div
                                    className={this.state.currentTab === 1 ? styles_2.active : null}>{this.props.descr}</div>
                                : this.state.currentTab === 2 ?
                                <div className={this.state.currentTab === 2 ? styles_2.active : null}>Специфічні
                                    параметри</div>
                                : <div
                                    className={this.state.currentTab === 3 ? styles_2.active : null}>{this.props.comments}</div>
                        }

                    </div>
                </div>
            </div>
        )
    }
}

export default Card;