import React from 'react';
import styles from './Menu.module.scss'
import {NavLink} from 'react-router-dom';

const Menu = () => {
    return (
        <nav>
            <ul className={styles.menu}>
                <li><NavLink activeClassName={styles.active} to='/home'>Головна</NavLink></li>
                <li><NavLink activeClassName={styles.active} to='/basket'>Корзина</NavLink></li>
                <li><NavLink activeClassName={styles.active} to='/GBOproducers'>Виробники ГБО</NavLink></li>
                <li><NavLink activeClassName={styles.active} to='/aboutUs'>Про нас</NavLink></li>
            </ul>
        </nav>
    )
};

export default Menu;


