//other import
import React from 'react';
import styles from './SearchField.module.scss'
//my components

const SearchField = () => {
    return (
        <div className={styles.searchField}>
            <p>Пошук по назві:</p>
            <input/>
            <div className={styles.button}>знайти</div>
        </div>
    )
};

export default SearchField;


