//other import
import React from 'react';
import styles from './../../../scss/commonComponents/Products.module.scss';
//components
import Card from '../Card/Card';

const Products = (props) => {

    let cardsAfterMap = props.productsData.map(unit => <Card category={props.match.params.category}
                                                             key={unit.id} {...unit}/>);

    let cardsAfterMapNew = props.productsDataNew.map(unit => <Card category={props.match.params.category}
                                                                   key={unit.id} {...unit}/>);

    let cardsAfterMapAction = props.productsDataAction.map(unit => <Card category={props.match.params.category}
                                                                         key={unit.id} {...unit}/>);

    let shit = () => {
        if (props.match.path !== '/home') {
            return cardsAfterMap;
        } else {
            return [cardsAfterMapNew, cardsAfterMapAction]
        }
    };

    return (
        <div className={`${styles.products} container`}>
            {shit()}
        </div>
    )
};

export default Products;


