//other import
import React from 'react';
import styles from '../scss/App.module.scss';
import backgroundImage from './../assets/img/backgroundImage.jpg';
import {Route} from "react-router-dom";
//components
import Header from "./staticСomponents/Header/Header";
import SearchField from "./staticСomponents/SearchField/SearchField";
import SecondaryMenu from "./staticСomponents/SecondaryMenu/SecondaryMenu";
import ProductsContainer from "./commonComponents/Products/ProductsContainer";
import MoreDetail from "./commonComponents/MoreDetail/MoreDetail";
import PurchaseOne from "./commonComponents/PurchaseOne/PurchaseOne";
import AboutUs from "./dynamicComponents/AboutUs/AboutUs";
import Home from "./dynamicComponents/Home/Home";
import Basket from "./dynamicComponents/Basket/Basket";

function App() {
    return (
        <div style={{backgroundImage: `url(${backgroundImage})`}} className={styles.app}>
            <div className={styles.eclipse}>

            </div>
            <div className={styles.appContent}>
                <Header/>
                <SearchField/>
                <SecondaryMenu/>
                <Route lazy path='/home' render={() => <Home/>}/>
                <Route lazy path='/basket' render={() => <Basket/>}/>
                <Route lazy path='/aboutUs' render={() => <AboutUs/>}/>
                <Route lazy path='/products/:category' render={() => <ProductsContainer/>}/>
                <Route lazy path='/moreDetail' render={() => <MoreDetail/>}/>
                <Route lazy path='/purchaseOne' render={() => <PurchaseOne/>}/>
            </div>
        </div>
    );
}

export default App;
