import React from 'react';
import styles from './../../../scss/commonComponents/Form.module.scss';

const Form = () => {
    return (
        <div>
            <form className={styles.form}>
                <input placeholder="Як до вас звертатись?" type="text"/>
                <input placeholder="Номер телефону" type="text"/>
                <input placeholder="Номер поштового відділеня" type="text"/>
            </form>
        </div>
    )
};

export default Form;