//other import
import React from 'react';
import styles from '../../../scss/staticСomponents/SecondaryMenu.module.scss';
import img1 from './../../../assets/img/HBOkits.svg';
import img2 from './../../../assets/img/injectionSystems.svg';
import img3 from './../../../assets/img/gasReducersAndComponents.svg';
import img4 from './../../../assets/img/nozzles.svg';
import img5 from './../../../assets/img/filters.svg';
import img6 from './../../../assets/img/electronicsGasAndGasolineValves.svg';
import img7 from './../../../assets/img/tubesHosesAccessories.svg';
import img8 from './../../../assets/img/cylindersFittingsFasteners.svg';
import img9 from './../../../assets/img/gasDispensersMixersAndAntiSlap.svg';
import img10 from './../../../assets/img/STOequipmentProtectiveLiquids.svg';

import Section from "./Section/Section";

const SecondaryMenu = () => {

    const sectionData = [
        {id: 1, text: 'Комплекти гбо', img: img1, to: '/products/HBOkits'},
        {id: 2, text: 'Інжекторні системи (електроніка)', img: img2, to: '/products/injectionSystems'},
        {id: 3, text: 'Газові редуктори та комплектуючі', img: img3, to: '/products/gasReducersAndComponents'},
        {id: 4, text: 'Форсунки', img: img4, to: '/products/nozzles'},
        {id: 5, text: 'Фільтри', img: img5, to: '/products/filters'},
        {id: 6, text: 'Електроніка, клапана газу і бензину', img: img6, to: '/products/electronicsGasAndGasolineValves'},
        {id: 7, text: 'Трубки, шланги та комплектуючі', img: img7, to: '/products/tubesHosesAccessories'},
        {id: 8, text: 'Балони, арматура, кріплення і ВЗУ', img: img8, to: '/products/cylindersFittingsFasteners'},
        {id: 9, text: 'Дозатори газу, змішувачі та антихлопки', img: img9, to: '/products/gasDispensersMixersAndAntiSlap'},
        {id: 10, text: 'Устаткування для СТО, захисні рідини', img: img10, to: '/products/STOequipmentProtectiveLiquids'},
    ];

    const sectionAfterMap = sectionData.map((unit) => <Section to={unit.to} key={unit.text} text={unit.text}
                                                               img={unit.img}/>);

    return (
        <div className={styles.secondaryMenuWrapper}>
            <div className={`container ${styles.secondaryMenu}`}>
                {sectionAfterMap}
            </div>
        </div>
    )
};

export default SecondaryMenu;


