let initialState = {
    productsData: [],
    productsDataNew: [],
    productsDataAction: [],
};

export const ProductsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_PRODUCTS_DATA: {
            return {
                ...state, productsData: action.productsData,
            }
        }
        case SET_PRODUCTS_DATA_NEW_AND_ACTION: {
            return {
                ...state, productsDataNew: action.productsDataNew, productsDataAction: action.productsDataAction
            }
        }
        default: {
            return state;
        }
    }
};
//action types
const SET_PRODUCTS_DATA = 'SET_PRODUCTS_DATA';
const SET_PRODUCTS_DATA_NEW_AND_ACTION = 'SET_PRODUCTS_DATA_NEW';
//action creators
export const setProductsData = (productsData) => ({type: SET_PRODUCTS_DATA, productsData});
export const setProductsDataNewAndAction = (productsDataNew, productsDataAction) => ({type: SET_PRODUCTS_DATA_NEW_AND_ACTION, productsDataNew, productsDataAction});
