import React from 'react';
import styles from './../../../scss/commonComponents/Check.module.scss';

const Check = (props) => {

    let wantToBuyAfterMap = props.wantToBuyArray.map(unit => <tr key={unit.id}>
        <td>{unit.name}</td>
        <td>{unit.id}</td>
        <td>{unit.wantToBuy}</td>
        <td>{unit.price}</td>
        <td>{unit.price * unit.wantToBuy}</td>
    </tr>);

    return (
        <div className={styles.tableWrapper}>
            <table className={styles.table}>
                <tbody>
                <tr>
                    <td>Назва товару</td>
                    <td>Код товару</td>
                    <td>Кількість</td>
                    <td>Ціна за 1шт.</td>
                    <td>Загальна ціна</td>
                </tr>
                {wantToBuyAfterMap}
                </tbody>
            </table>
        </div>
    )
};

export default Check;

